<template>
  <div class="Boards-page">
    <!--  &  -->
    <v-row no-gutters class="d-flex justify-space-between mt-10 mb-6">
      <h1 class="page-title">CIMI | </h1>
    </v-row>

    <!------------ !------------>
    <v-data-table
      :headers="headers"
      :items="tableData"
      :search="search"
      sort-by="account"
      class="elevation-1"
    >
      <!-- slot:  -->
      <template v-slot:[`item.mem_power`]="{ item }">
        <v-chip class="ma-2" :color="powerColor(item.mem_power)" outlined pill>
          {{ memPower(item.mem_power) }}
          <v-icon right> mdi-account-outline </v-icon>
        </v-chip>
      </template>

      <!-- slot:  -->
      <template v-slot:[`item.mem_state`]="{ item }">
        <v-chip :color="getColor(item.mem_state)">
          {{ State(item.mem_state) }}
        </v-chip>
      </template>

      <!-- SLOT -->
      <template v-slot:[`item.actions`]="{ item }">
        <v-icon small class="mr-2" @click="editItem(item)"> mdi-pencil </v-icon>
        <v-icon small @click="deleteItem(item)"> mdi-delete </v-icon>
      </template>

      <!-- SLOT -->
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title></v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <v-spacer></v-spacer>

          <!--  -->
          <v-text-field
            hide-details
            append-icon="mdi-magnify"
            single-line
            class="shrink pr-6"
            v-model="search"
            label="Search"
          ></v-text-field>

          <!-- CSV -->
          <download-csv :data="tableData">
            <v-btn icon alt="CSV" title="">
              <v-icon class="mdi-36px pl-2 pr-2">
                mdi-arrow-down-bold-box
              </v-icon>
            </v-btn>
          </download-csv>

          <!-- dialog -->
          <v-dialog
            v-model="dialog"
            @save="save"
            @cancel="cancel"
            max-width="400"
          >
            <v-card class="mx-auto" max-width="400" tile>
              <v-card-title>
                <span class="text-h5"></span>
              </v-card-title>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-account</v-icon>
                  <span class="text-subtitle-1">ID :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.mem_no }}
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-account</v-icon>
                  <span class="text-subtitle-1"> :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.mem_id }}
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-account</v-icon>
                  <span class="text-subtitle-1"> :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.mem_name }}
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-account-edit</v-icon>
                  <span class="text-subtitle-1"> :</span>
                </div>
                <div class="meminfo">
                  <v-select
                    :items="powers"
                    item-text="item"
                    item-value="val"
                    v-model="editedItem.mem_power"
                    class="selector"
                  ></v-select>
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-account-edit</v-icon>
                  <span class="text-subtitle-1"> :</span>
                </div>
                <div class="meminfo">
                  <v-select
                    :items="states"
                    item-text="item"
                    item-value="val"
                    v-model="editedItem.mem_state"
                    width="80px"
                    class="selector"
                  ></v-select>
                </div>
              </div>

              <v-spacer></v-spacer>

              <div class="btn">
                <v-btn color="blue darken-1" text @click="dialog = false"
                  ></v-btn
                >
                <v-btn color="blue darken-1" text @click="save(editedIndex)"
                  ></v-btn
                >
              </div>
            </v-card>
          </v-dialog>

          <!-- dialog -->
          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="text-h5">
                 ?</v-card-title
              >
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete"
                  ></v-btn
                >
                <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                  ></v-btn
                >
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
    </v-data-table>
    <!----------- !------------>
  </div>
</template>

<script>
export default {
  inject: ["reload"],
  data: () => ({
    search: "", // v-model
    dialog: false, // EDIT DIALOG 
    dialogDelete: false, // DELETE DIALOG 
    dialogNew: false,
    tableData: [], // 
    headers: [
      // 
      {
        text: "ID",
        align: "start",
        value: "mem_no",
      },
      { text: "", value: "mem_id" },
      { text: "", value: "mem_name" },
      { text: "", value: "mem_power" },
      { text: "", value: "mem_state" },
      { text: "", value: "actions", sortable: false }, // sortable => 
    ],
    powers: [
      { val: 0, item: "" },
      { val: 1, item: "" },
      { val: 2, item: "" },
    ],
    states: [
      { val: 0, item: "" },
      { val: 1, item: "" },
    ],
    editedIndex: -1, //  index
    editedItem: {
      //  
      billboard_no: "",
      publish_date: null,
      article_title: "",
      article_state: "0",
      article_content: "",
    },
    defaultItem: {
      //  
      billboard_no: "",
      publish_date: null,
      article_title: "",
      article_state: "0",
      article_content: "",
    },
    // today:this.editDate,
  }),
  mounted() {
    //  PHPsql, 
    this.axios
      .get("https://tibamef2e.com/cfd104/g4/back/api/mems.php")
      .then((res) => {
        console.log(res);
        this.tableData = res.data;
      })
      .catch((err) => {
        console.log(err);
      });
  },
  computed: {
    formTitle() {
      // dialog => if true new item, else edit item
      return this.editedIndex === -1 ? "New Item" : "Edit Item";
    },
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  created() {
    this.initialize();
  },
  methods: {
    initialize() {
      this.tableData = [
        {
          name: "",
          account: "2022-02-27",
          access: "7pupu",
          status: "",
        },
        {
          name: "",
          account: "2022-02-26",
          access: "",
          status: "",
        },
      ];
    },
    State: (v) => {
      // 0/1 -> 
      if (v == 0) {
        return "";
      } else {
        return "";
      }
    },
    memPower: (v) => {
      if (v == "1") {
        return "";
      } else if (v == "2") {
        return "";
      } else {
        return "";
      }
    },

    getColor(v) {
      // state  v-chip 
      if (v == 1) return "default";
      else if (v == 0) return "primary";
    },
    powerColor(v) {
      if (v == 0) {
        return "default";
      } else if (v == 1) {
        return "primary";
      } else {
        return "success";
      }
    },

    editItem(item) {
      //
      this.editedIndex = this.tableData.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      //
      this.editedIndex = this.tableData.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      this.dialogDelete = false;
      let params = new URLSearchParams();
      params.append("mem_no", this.editedItem.mem_no);
      params.append("state", "delete");
      this.axios
        .post("https://tibamef2e.com/cfd104/g4/back/api/editmem.php", params)
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
      this.tableData.splice(this.editedIndex, 1);
    },

    close() {
      this.dialog = false;
      this.dialogNew = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    save() {
      //for 
      Object.assign(this.tableData[this.editedIndex], this.editedItem);
      this.dialog = false;
      let params = new URLSearchParams();
      params.append("mem_no", this.editedItem.mem_no);
      params.append("mem_power", this.editedItem.mem_power);
      params.append("mem_state", this.editedItem.mem_state);
      params.append("state", "renew");
      this.axios
        .post("https://tibamef2e.com/cfd104/g4/back/api/editmem.php?case=2", params)
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>

<style scoped>
.txt {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0 36px 0 24px;
  margin: 10px 0;
}
.selector {
  width: 150px;
}
.btn {
  display: flex;
  justify-content: space-evenly;
  padding-bottom: 20px;
}
</style>