<template>
  <div class="Boards-page">
    <!--  -->
    <v-row no-gutters class="d-flex justify-space-between mt-10 mb-6">
      <h1 class="page-title">CIMI | </h1>
    </v-row>

    <!------------ !------------>
    <v-data-table
      :headers="headers"
      :items="tableData"
      :search="search"
      sort-by="account"
      class="elevation-1"
    >
      <!-- slot: 0: 1: -->
      <template v-slot:[`item.report_state`]="{ item }">
        <v-chip :color="getColor(item.report_state)">
          {{ State(item.report_state) }}
        </v-chip>
      </template>

      <!-- slot: 0: 1: 2: -->
      <template v-slot:[`item.report_report`]="{ item }">
        <v-chip :color="reportColor(item.report_report)">
          {{ report(item.report_report) }}
        </v-chip>
      </template>

      <!-- slot:  -->
      <template v-slot:[`item.comment_content`]="{ item }">
        <tr>
          <td class="truncate">{{ item.comment_content }}</td>
        </tr>
      </template>

      <!-- SLOT -->
      <template v-slot:[`item.actions`]="{ item }">
        <v-icon small class="mr-2" @click="editItem(item)"> mdi-pencil </v-icon>
        <v-icon small @click="deleteItem(item)"> mdi-delete </v-icon>
      </template>

      <!-- SLOT -->
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title></v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <v-spacer></v-spacer>

          <!--  -->
          <v-text-field
            hide-details
            append-icon="mdi-magnify"
            single-line
            class="shrink pr-6"
            v-model="search"
            label="Search"
          ></v-text-field>

          <!-- CSV -->
          <download-csv :data="tableData">
            <v-btn icon title="Download CSV">
              <v-icon class="mdi-36px pl-2 pr-2">
                mdi-arrow-down-bold-box
              </v-icon>
            </v-btn>
          </download-csv>

          <!-- dialog -->
          <v-dialog
            v-model="dialog"
            @save="save"
            @cancel="cancel"
            max-width="450"
          >
            <v-card class="mx-auto" max-width="450" tile>
              <v-card-title>
                <span class="text-h5"></span>
              </v-card-title>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-format-align-left</v-icon>
                  <span class="text-subtitle-1"> :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.comment_no }}
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-calendar-clock</v-icon>
                  <span class="text-subtitle-1"> :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.report_date }}
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-sim-alert</v-icon>
                  <span class="text-subtitle-1"> :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.report_reason }}
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-account-alert</v-icon>
                  <span class="text-subtitle-1"> :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.mem_name }}
                </div>
              </div>

              <div class="article_content">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-comment-text</v-icon>
                  <span class="text-subtitle-1"> :</span>
                </div>
                <div class="meminfo">
                  <v-textarea
                    :value="editedItem.comment_content"
                    filled
                  ></v-textarea>
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-message-alert</v-icon>
                  <span class="text-subtitle-1"> :</span>
                </div>
                <div class="meminfo">
                  <v-select
                    :items="stateSelect"
                    item-text="item"
                    item-value="val"
                    v-model="editedItem.report_report"
                    width="80px"
                    class="selector"
                  ></v-select>
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-message-alert</v-icon>
                  <span class="text-subtitle-1"> :</span>
                </div>
                <div class="meminfo">
                  <v-select
                    :items="reportSelect"
                    item-text="item"
                    item-value="val"
                    v-model="editedItem.report_report"
                    width="80px"
                    class="selector"
                  ></v-select>
                </div>
              </div>

              <v-spacer></v-spacer>

              <div class="btn">
                <v-btn color="blue darken-1" text @click="dialog = false"
                  ></v-btn
                >
                <v-btn color="blue darken-1" text @click="save(editedIndex)"
                  ></v-btn
                >
              </div>
            </v-card>
          </v-dialog>

          <!-- dialog -->
          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="text-h5">
                 ?</v-card-title
              >
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete"
                  ></v-btn
                >
                <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                  ></v-btn
                >
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
    </v-data-table>
    <!----------- !------------>
  </div>
</template>

<script>
export default {
  inject: ["reload"], //!
  data: () => ({
    search: "", // v-model
    dialog: false, // EDIT DIALOG 
    dialogDelete: false, // DELETE DIALOG 
    tableData: [], // 
    headers: [
      // 
      {
        text: "",
        align: "start",
        value: "comment_no",
      },
      { text: "", value: "report_date" },
      { text: "", value: "report_reason" },
      { text: " ", value: "mem_name" },
      { text: "", value: "report_state" },
      { text: "", value: "report_report" },
      { text: "", value: "actions", sortable: false }, // sortable => 
    ],
    stateSelect: [
      //  val & item
      { val: 0, item: "" },
      { val: 1, item: "" },
    ],
    reportSelect: [
      //  val & item
      { val: 0, item: "" },
      { val: 1, item: "" },
      { val: 2, item: "" },
    ],
    editedIndex: -1, //  index
    editedItem: {
      //  
      article_title: "",
      report_date: "",
      mem_name: "",
      report_report: "",
      report_state: "",
    },
    defaultItem: {
      //  
      article_title: "",
      report_date: "",
      mem_name: "",
      report_report: "",
      report_state: "",
    },
    time: "", //  FOR   =>  Bug QQ
  }),
  mounted() {
    // PHPsql, 
    let params = new URLSearchParams();
    params.append("case", "comment");
    this.axios
      .post("https://tibamef2e.com/cfd104/g4/back/api/reports.php", params)
      .then((res) => {
        this.tableData = res.data;
      })
      .catch((err) => {
        console.log(err);
      });
  },
  computed: {},
  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  created() {
    this.initialize();
  },
  methods: {
    initialize() {
      this.tableData = [
        {
          name: "",
          account: "2022-02-27",
          access: "7pupu",
          status: "",
        },
        {
          name: "",
          account: "2022-02-26",
          access: "",
          status: "",
        },
      ];
    },
    State: (v) => {
      // 0/1 -> 
      if (v == 0) {
        return "";
      } else {
        return "";
      }
    },
    report: (v) => {
      // 0/1 -> 
      if (v == 0) {
        return "";
      } else if (v == 1) {
        return "";
      } else {
        return "";
      }
    },

    getColor(state) {
      // state  v-chip 
      if (state == 1) return "default";
      else if (state == 0) return "primary";
    },

    reportColor(state) {
      // state  v-chip 
      if (state == 0) {
        return "default";
      } else if (state == 1) {
        return "primary";
      } else {
        return "secondary";
      }
    },

    editItem(item) {
      //
      this.editedIndex = this.tableData.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      //
      this.editedIndex = this.tableData.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      let params = new URLSearchParams();
      params.append("comment_report_no", this.editedItem.comment_report_no);
      params.append("phpState", "deleteComment");
      this.axios
        .post("https://tibamef2e.com/cfd104/g4/back/api/editreport.php", params)
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
      this.tableData.splice(this.editedIndex, 1);
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.dialogNew = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    save() {
      //for 
      if (this.editedIndex > -1) {
        Object.assign(this.tableData[this.editedIndex], this.editedItem);
        let params = new URLSearchParams();
        params.append("comment_report_no", this.editedItem.comment_report_no);
        params.append("report_report", this.editedItem.report_report);
        params.append("report_state", this.editedItem.report_state);
        params.append("phpState", "commentRenew");
        this.axios
          .post("https://tibamef2e.com/cfd104/g4/back/api/editreport.php", params)
          .then((res) => {
            console.log(res);
          })
          .catch((err) => {
            console.log(err);
          });
      } else {
        return false;
      }
      this.close();
    },
    createNew() {
      //for 
      let params = new URLSearchParams();
      params.append("billboard_name", this.editedItem.billboard_name);
      params.append("billboard_state", this.editedItem.billboard_state);
      params.append("mem_name", this.editedItem.mem_name);
      params.append("mem_no", this.editedItem.mem_no);
      params.append("phpState", "createNew");
      this.axios
        .post("https://tibamef2e.com/cfd104/g4/back/api/editbillboard.php", params)
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
      this.tableData.push(this.editedItem);
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
      this.close();
      this.reload();
    },
  },
};
</script>

<style scoped>
::v-deep .v-label {
  /* transform: translateY(-20px); */
  height: 30px;
}
.txt {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0 36px 0 24px;
  margin: 10px 0;
}
.article_content {
  padding: 0 36px 0 24px;
  margin: 10px 0;
}
.selector {
  width: 150px;
}
.btn {
  display: flex;
  justify-content: space-evenly;
  padding-bottom: 20px;
}
.img {
  max-width: 150px;
  width: 150px;
  height: 150px;
  padding: 10px;
}
img {
  width: 100%;
}
</style>