<template>
  <div class="Boards-page">
    <!--  &  -->
    <v-row no-gutters class="d-flex justify-space-between mt-10 mb-6">
      <h1 class="page-title">CIMI | </h1>
      <!-- dialog -->
    </v-row>

    <!------------ !------------>
    <v-data-table
      :headers="headers"
      :items="tableData"
      :search="search"
      sort-by="account"
      class="elevation-1"
    >
      <!-- slot: 0:1:" -->
      <template v-slot:[`item.order_way`]="{ item }">
        <v-chip :color="wayColor(item.order_way)">
          {{ way(item.order_way) }}
        </v-chip>
      </template>

      <!-- slot: 0:1:2:3:4:"-->
      <template v-slot:[`item.order_shipping`]="{ item }">
        <v-chip :color="shipColor(item.order_shipping)">
          {{ ship(item.order_shipping) }}
        </v-chip>
      </template>

      <!-- SLOT -->
      <template v-slot:[`item.actions`]="{ item }">
        <v-icon small class="mr-2" @click="editItem(item)"> mdi-pencil </v-icon>
        <v-icon small @click="deleteItem(item)"> mdi-delete </v-icon>
      </template>

      <!-- SLOT -->
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title></v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <v-spacer></v-spacer>

          <!--  -->
          <v-text-field
            hide-details
            append-icon="mdi-magnify"
            single-line
            class="shrink pr-6"
            v-model="search"
            label="Search"
          ></v-text-field>

          <!-- CSV -->
          <download-csv :data="tableData">
            <v-btn icon>
              <v-icon class="mdi-36px pl-2 pr-2">
                mdi-arrow-down-bold-box
              </v-icon>
            </v-btn>
          </download-csv>

          <!-- dialog -->
          <v-dialog
            v-model="dialog"
            @save="save"
            @cancel="cancel"
            max-width="400"
          >
            <v-card class="mx-auto" max-width="450" tile>
              <v-card-title>
                <span class="text-h5"></span>
              </v-card-title>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-alert-circle-outline</v-icon>
                  <span class="text-subtitle-1"> :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.order_no }}
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-calendar-clock</v-icon>
                  <span class="text-subtitle-1"> :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.order_time }}
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-account</v-icon>
                  <span class="text-subtitle-1"> :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.order_buyer_no }}
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-account</v-icon>
                  <span class="text-subtitle-1"> :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.mem_name }}
                </div>
              </div>

              <v-divider></v-divider>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-cash-100</v-icon>
                  <span class="text-subtitle-1"> :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.order_tp }}
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-minus</v-icon>
                  <span class="text-subtitle-1"> :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.order_price_minus }}
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-equal</v-icon>
                  <span class="text-subtitle-1">() :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.order_real_price }}
                </div>
              </div>

              <v-divider></v-divider>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-truck</v-icon>
                  <span class="text-subtitle-1"> :</span>
                </div>
                <div class="meminfo">
                  <v-chip :color="wayColor(editedItem.order_shipping)">
                    {{ way(editedItem.order_way) }}
                  </v-chip>
                </div>
              </div>

              <!-- <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-message-alert</v-icon>
                  <span class="text-subtitle-1"> :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.order_place }}
                </div>
              </div> -->

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-message-alert</v-icon>
                  <span class="text-subtitle-1"> :</span>
                </div>
                <div class="meminfo">
                  <v-chip :color="shipColor(editedItem.order_shipping)">
                    {{ ship(editedItem.order_shipping) }}
                  </v-chip>
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-thumb-up</v-icon>
                  <span class="text-subtitle-1"> :</span>
                </div>
                <div class="meminfo">
                  {{ satisfy(editedItem.order_satisfy) }}
                </div>
              </div>

              <v-spacer></v-spacer>

              <div class="btn">
                <v-btn color="blue darken-1" text @click="close">  </v-btn>
              </div>
            </v-card>
          </v-dialog>

          <!-- dialog -->
          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="text-h5">
                 ?</v-card-title
              >
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete"
                  ></v-btn
                >
                <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                  ></v-btn
                >
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
    </v-data-table>
    <!----------- !------------>
  </div>
</template>

<script>
export default {
  inject: ["reload"], //!
  data: () => ({
    search: "", // v-model
    dialog: false, // EDIT DIALOG 
    dialogDelete: false, // DELETE DIALOG 
    dialogNew: false,
    tableData: [], // 
    mems: [],
    headers: [
      // 
      {
        text: "",
        align: "start",
        value: "order_no",
      },
      { text: "", value: "order_time" },
      { text: " - ", value: "order_buyer_no" },
      { text: " - ", value: "mem_name" },
      { text: "", value: "order_way" },
      { text: "", value: "order_shipping" },
      { text: "", value: "actions", sortable: false }, // sortable => 
    ],
    boards: [],
    manu: [],
    order_way: [
      //  val & item
      { val: 0, item: "" },
      { val: 1, item: "" },
    ],
    order_shipping: [
      //  val & item
      { val: 0, item: "" },
      { val: 1, item: "" },
      { val: 2, item: "" },
      { val: 3, item: "" },
      { val: 4, item: "" },
    ],
    editedIndex: -1, //  index
    editedItem: {
      //  
      order_no: "",
      order_time: null,
      order_buyer_no: "",
      mem_name: "",
      order_way: "",
      order_shipping: "0",
      order_tp: "0",
      order_price_minus: "0",
      order_real_price: "0",
      order_place: "0",
      order_over_time: "0",
      order_satisfy: "0",
    },
    defaultItem: {
      //  
      order_no: "",
      order_time: null,
      order_buyer_no: "",
      mem_name: "",
      order_way: "",
      order_shipping: "0",
      order_tp: "0",
      order_price_minus: "0",
      order_real_price: "0",
      order_place: "0",
      order_over_time: "0",
      order_satisfy: "0",
    },
    time: "", //  FOR   =>  Bug QQ
  }),
  mounted() {
    //  PHPsql, 
    this.axios
      .get("https://tibamef2e.com/cfd104/g4/back/api/orders.php")
      .then((res) => {
        this.tableData = res.data;
        let cate = res.data.map((item) => Object.values(item)[7]);
        let cateArr = cate.filter((ele, pos) => cate.indexOf(ele) == pos);
        this.boards = cateArr;

        let manu = res.data.map((item) => Object.values(item)[6]);
        let manuArr = manu.filter((ele, pos) => manu.indexOf(ele) == pos);
        this.manu = manuArr;
      })
      .catch((err) => {
        console.log(err);
      });
  },
  computed: {},
  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  created() {
    this.initialize();
  },
  methods: {
    initialize() {
      this.tableData = [
        {
          name: "",
          account: "2022-02-27",
          access: "7pupu",
          status: "",
        },
        {
          name: "",
          account: "2022-02-26",
          access: "",
          status: "",
        },
      ];
    },
    way: (v) => {
      // 0/1 -> 
      if (v == 0) {
        return "";
      } else if (v == 1) {
        return "";
      }
    },
    wayColor(state) {
      // state  v-chip 
      if (state == 1) {
        return "default";
      } else if (state == 0) {
        return "primary";
      }
    },
    ship: (v) => {
      // 0/1 -> 
      if (v == 0) {
        return "";
      } else if (v == 1) {
        return "";
      } else if (v == 2) {
        return "";
      } else if (v == 3) {
        return "";
      } else {
        return "";
      }
    },
    shipColor(v) {
      // ship  v-chip 
      if (v == 0) {
        return "secondary";
      } else if (v == 1) {
        return "warning";
      } else if (v == 2) {
        return "primary";
      } else if (v == 3) {
        return "success";
      } else {
        return "dark";
      }
    },

    satisfy(v) {
      //   v-chip 
      if (v == 1) {
        return "";
      } else if (v == 2) {
        return "";
      } else if (v == 3) {
        return "";
      } else if (v == 4) {
        return "";
      } else {
        return "";
      }
    },

    editItem(item) {
      //
      this.editedIndex = this.tableData.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      //
      this.editedIndex = this.tableData.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      let params = new URLSearchParams();
      params.append("order_no", this.editedItem.order_no);
      params.append("phpState", "delete");
      this.axios
        .post("https://tibamef2e.com/cfd104/g4/back/api/editorder.php", params)
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
      this.tableData.splice(this.editedIndex, 1);
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.dialogNew = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    save() {
      //for 
      if (this.editedIndex > -1) {
        Object.assign(this.tableData[this.editedIndex], this.editedItem);
        let params = new URLSearchParams();
        params.append("goods_class", this.editedItem.goods_class);
        params.append("goods_name", this.editedItem.goods_name);
        params.append("goods_i", this.editedItem.goods_i);
        params.append("goods_price", this.editedItem.goods_price);
        params.append("goods_firm", this.editedItem.goods_firm);
        params.append("goods_st", this.editedItem.goods_st);
        params.append("goods_no", this.editedItem.goods_no);
        params.append("phpState", "renew");
        this.axios
          .post("https://tibamef2e.com/cfd104/g4/back/api/editgood.php", params)
          .then((res) => {
            console.log(res);
          })
          .catch((err) => {
            console.log(err);
          });
      } else {
        return false;
      }
      this.close();
    },
  },
};
</script>

<style lang="scss" scoped>
.txt {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0 36px 0 24px;
  margin: 10px 0;
}
.btn {
  display: flex;
  justify-content: flex-end;
  padding: 20px 20px 10px 0;
}
</style>